// import React, { useRef, useEffect } from "react";
import React, { useState, useRef, useEffect } from "react";
import { Camera } from "@mediapipe/camera_utils";
import { Pose, POSE_CONNECTIONS } from "@mediapipe/pose";
import "@mediapipe/drawing_utils/drawing_utils";
import { drawConnectors, drawLandmarks } from "@mediapipe/drawing_utils";

function App() {
  const canvasElement = useRef(null);
  const videoElement = useRef(null);
  const videoConstraints = {
    width: 640,
    height: 360,
  };
  const [count, setCount] = useState(0);
  const isCurling = useRef(false);
  

  useEffect(() => {
    const pose = new Pose({
      locateFile: (file) => {
        return `https://cdn.jsdelivr.net/npm/@mediapipe/pose/${file}`;
      },
    });
  
    pose.setOptions({
      modelComplexity: 1,
      smoothLandmarks: true,
      enableSegmentation: true,
      smoothSegmentation: true,
      minDetectionConfidence: 0.5,
      minTrackingConfidence: 0.5,
    });
  
    pose.onResults(onResults);
  
    if (navigator.mediaDevices && navigator.mediaDevices.getUserMedia) {
      navigator.mediaDevices
        .getUserMedia({ video: videoConstraints })
        .then((stream) => {
          videoElement.current.srcObject = stream;
          videoElement.current.onloadedmetadata = () => {
            const camera = new Camera(videoElement.current, {
              onFrame: async () => {
                await pose.send({ image: videoElement.current });
              },
              width: videoConstraints.width,
              height: videoConstraints.height,
            });
            camera.start();
          };
        })
        .catch((err) => {
          console.error("Failed to get user media", err);
        });
    }
  
    // Cleanup function
    return () => {
      pose.close();
    };
  }, []);
  

  function onResults(results) {
    const canvasCtx = canvasElement.current.getContext("2d");
    canvasCtx.save();
    canvasCtx.clearRect(
      0,
      0,
      canvasElement.current.width,
      canvasElement.current.height
    );
    canvasCtx.drawImage(
      results.image,
      0,
      0,
      canvasElement.current.width,
      canvasElement.current.height
    );
    drawConnectors(
      canvasCtx,
      results.poseLandmarks,
      POSE_CONNECTIONS,
      { color: "#00FF00", lineWidth: 2 }
    );
    drawLandmarks(
      canvasCtx,
      results.poseLandmarks,
      { color: "#FF0000", lineWidth: 1, radius: 3 } // ここにスペースを追加
    );
    if (results.poseLandmarks) {
      const leftWrist = results.poseLandmarks[9]; // 左手首
      const rightWrist = results.poseLandmarks[10]; // 右手首

      // アームカール動作を検出するための簡単なアルゴリズム：
      // 手首がある特定の高さ以上にある場合、それをアームカールとしてカウントする。
      if (leftWrist.visibility > 0.5 || rightWrist.visibility > 0.5) {
        if (!isCurling.current && (leftWrist.y < 0.5 || rightWrist.y < 0.5)) {
          isCurling.current = true;
        } else if (isCurling.current && leftWrist.y > 0.5 && rightWrist.y > 0.5) {
          setCount(count + 1);
          isCurling.current = false;
        }
      }
    }
    canvasCtx.restore();
  }
  
    return (
      <div className="App" style={{ display: "flex", justifyContent: "center", alignItems: "center", height: "100vh", lexDirection: 'column' }}>
        <header className="App-header">
          <video
            ref={videoElement}
            style={{
              display: "none" // 動画を非表示にする
            }}
          />
          <canvas
            ref={canvasElement}
            style={{
              // position: "relative",
              // marginLeft: "auto",
              // marginRight: "auto",
              // left: 0,
              // right: 0,
              top: "80%",
              // transform: "translateY(-50%)",
              // textAlign: "center",
              // zIndex: 9,
              width: videoConstraints.width,
              height: videoConstraints.height,
            }}
          />
          <h1 style={{ zIndex: 10, position: 'absolute', color: '#000' }}>アームカールの回数： {count}</h1>
        </header>
      </div>
    );

  }
  
  export default App;
  